@import "../colors.module.scss";
@import "../globals.module.scss";

$textColor: $grey11;
$textColorHover: $white;

.pageTitle {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 57px 22px;
  position: relative;
  z-index: 1;
  border-bottom: 1px solid $grey09;
  background: $grey15;
}

.pageTitleName {
  font-size: 26px;
  font-family: "Open Sans Bold", sans-serif;
  display: flex;
  text-transform: uppercase;
  color: $textColor;
  flex-grow: 1;
  align-items: center;
}

.currentAppActions {
  color: $textColor;
  justify-content: center;
  align-items: center;
  display: flex;
}

.actionItem {
  cursor: pointer;
  height: 68%;
  margin-left: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &:first-child {
    margin-left: 0;
  }

  @extend %hoverUnderline;
}

.actionItemText {
  padding-left: 5px;
}
