@import "../../components/colors.module.scss";

$borderColor: $grey16;
$borderColorHovered: $greyAlpha01;
$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

.pageTitleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.mkAppModalWrapper {
  height: 100%;
  max-width: 100%;
  width: 100%;
  border-radius: 6px;
}

.appName {
  text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  color: $grey12;
  margin-bottom: 12px;
  margin-left: 2px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.applicationTile {
  height: 100px;
  width: 185px;
  border: 1px solid $grey09;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px 10px;
  border-radius: 10px;
  background-color: $grey10;
  transition: $hoverTransition;

  &.withDndStyles {
    transition: $hoverTransition;
    transition: none;
    border: 1px solid $blueAlpha01;
    background: $blue01;
    box-shadow: 5px 5px 20px $greyAlpha02;
    opacity: 1;
    cursor: move;

    &:hover {
      .openAppInner {
        border: 1px solid $blueAlpha01;
        background: $blue02;
        transform: translateY(13px);
      }
    }

    .openAppInner {
      border: 1px solid $blueAlpha01;
      background: $blue02;
      transform: translateY(13px);
    }
  }

  &:hover,
  &.appIsLoading {
    box-shadow: 5px 5px 20px $greyAlpha02;
    border: 1px solid $greyAlpha01;

    .openAppInner {
      transition: $hoverTransition;
      transform: translateY(0);
    }
  }

  .openApp {
    position: absolute;
    bottom: 0;
    width: 90%;
    height: 18px;
    left: 10px;
    overflow: hidden;
    cursor: pointer;
  }

  .openAppInner {
    height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(13px);
    border-radius: 6px 6px 0 0;
    border: 1px solid $greyAlpha01;
    border-bottom: 0;
    background-color: $grey13;
    transition: transform 200ms ease-in-out;
  }

  .openAppText {
    height: 16px;
    text-transform: uppercase;
    color: rgb(118, 128, 147);
  }
}

.dndPlaceHolder {
  content: "";
  display: block;
  height: 100px;
  width: 185px;
  border-radius: 10px;
  border: 3px dashed $blue02;
  position: absolute;
}

.optionsMenu {
  width: 78px;
  height: 33px;
  position: absolute;
  right: 2px;
  top: 7px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: $grey05;
  padding: 5px 6px 5px 6px;
  transition: $hoverTransition;
}

.optionsItem:hover {
  color: $grey01;
  cursor: pointer;
}

svg.optionsMenuDnd {
  color: $blueAlpha01;

  &:hover {
    color: rgba(0, 151, 189, 0.96);
  }
}

.appIsLoading {
  pointer-events: none;
}

.pageContentWrapper {
  padding: 35px 56px 25px;
  width: 100%;
  height: 82vh;
  display: grid;
  grid-template-columns: repeat(auto-fill, 185px);
  grid-gap: 20px;
  grid-template-rows: repeat(auto-fill, 100px);
}

.apploader {
  background: rgb(236, 237, 244);
  position: absolute;
  top: 0px;
  left: 0px;
  border-radius: 10px;
  height: 98px;
  width: 183px;
}

.applicationTileWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  grid-gap: 20px;
  transition: $hoverTransition;
}

.thumbnailWrapper {
  position: absolute;
  top: 10px;
  left: 10px;
}
