@import "../colors.module.scss";

:global(#root) {
  height: 100%;
}

* {
  box-sizing: border-box;
}

.siteContent {
  position: relative;
  display: flex;
  height: 100%;
  min-height: 740px;
}

.pageRightWrapper {
  flex-grow: 1;
  background: $grey01;
  position: relative;
}

.pageWrapper {
  width: 100%;
  height: 95%;
  border-radius: 10px 0 0 10px;
  background: $white;
  position: relative;
  overflow: scroll;
  margin-top: 10px;
  box-shadow: 6px 4px 7px $grey18;
  opacity: 0;
  border: 1px solid $grey09;
  transform: translateX(65px);
  transition: all 320ms cubic-bezier(0.19, 1, 0.22, 1);
  will-change: transform, opacity;
}

.pageBasePlaceholder {
  position: absolute;
  width: 100%;
  height: 95%;
  border-radius: 10px 0 0 10px;

  background: $grey04;
  overflow: scroll;
  margin-top: 10px;
  box-shadow: 6px 4px 7px $grey18;
}

.slideInAnimation {
  opacity: 1;
  transform: translateX(0);
}

.slideOutAnimation {
  opacity: 0;
  transform: translateX(65);
}
