@import "../colors.module.scss";

$textColor: #e6e9ef;
$textColorHover: $white;
$iconHoverColor: #51bffb;
$disabled: $grey05;

@import "../colors.module.scss";
@import "./SideMenuSmallScreens.module.scss";

.sideMenuWrapper {
  min-width: 230px;
  background: $grey04;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 737px;
  position: relative;
}

.sideMenuTitle {
  color: $grey05;
  padding: 10px 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  font-size: 12px;
}

.sideMenuItem {
  text-shadow: 2px 2px 3px $grey04;
  color: $grey07;
  padding: 10px 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  display: block;
  font-size: 12px;
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
}

.sideMenuItemIcon {
  width: 0.825em;
  height: 25px;
  fill: currentColor;
  margin-right: 10px;
  vertical-align: middle;
  display: inline-block;

  .customHight {
    height: 25px;
  }
}

.sideMenuIPinnedApps {
  border-top: 1px solid $grey16;
  border-bottom: 1px solid $grey16;
  background: $grey13;
  padding: 10px 0;
  margin: 10px 0;
}

.sideMenuItem:hover {
  transition: background 100ms, color 100ms;
  color: $grey06;
  background: $grey08;
}

.selected {
  transition: background 100ms, color 100ms;

  &.sideMenuItem .sideMenuItemIcon {
    color: $iconHoverColor;
  }

  &.sideMenuItem {
    color: #e6e9ef;
    background-color: #252e40;
    border-top: 1px solid $grey18;
    border-bottom: 1px solid $grey18;
    text-shadow: 2px 2px 3px $grey04;
    padding: 9px 25px;
  }

  &.sideMenuItem:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 5px;
    color: $iconHoverColor;
    background: $iconHoverColor;
  }
}

.generalSectionWrapper {
  background: $grey01;
  padding: 20px 0;
  border-top: 1px solid $grey02;
  margin-top: 3px;
  border-bottom: 1px solid $grey03;
}

.pinnedSectionWrapper {
  border-top: 1px solid $grey02;
  margin-top: 3px;
  background: $grey01;
  padding: 20px 0;
  flex-grow: 1;
}

.pinnedApp {
  &.sideMenuItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &:hover .removePinnedAppIcon {
    opacity: 1;
    pointer-events: all;
  }

  .pinnedAppText {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 68%;
    align-items: center;
  }

  .pinnedAppTextSmallScreen {
    display: none;
  }

  .removePinnedAppIcon {
    transition: 200ms opacity, min-width cubic-bezier(0.075, 0.82, 0.165, 1);
    opacity: 0;
    pointer-events: none;
    height: 15px;
    min-width: 15px;
    color: $grey06;
    position: relative;
    left: -1px;

    &:hover {
      color: #b8bdc6;
    }
  }
}

.pinnedEmptyState {
  display: flex;
  padding: 10px 25px 12px;
  width: 229px;
  margin: auto;
  background-color: #252e40;
  border-top: 1px solid $grey18;
  border-bottom: 1px solid $grey18;
  text-shadow: 2px 2px 3px $grey04;
  color: $grey07;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  display: block;
  font-size: 12px;

  .explanation {
    font-size: 10px;
  }
}

.loggedInUserSectionWrapper {
  background: $grey01;
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  padding: 45px 0 30px;
  border-bottom: 1px solid $grey03;

  .userName {
    color: $grey07;
    text-shadow: 2px 2px 3px $grey04;

    &:hover {
      color: #e6e9ef;
      cursor: pointer;
    }
  }

  .userNameCollapsed {
    display: none;
  }

  .userThumbnail {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-image: url(../../assets/img/captain-sparrow.jpg);
    background-size: 150%;
    background-position-x: -14px;
    background-position-y: -4px;
    margin-bottom: 15px;
  }
}

.minimizeSideMenuIcon {
  cursor: pointer;
  color: $textColor;
  display: flex;
  justify-content: space-between;

  &:hover {
    color: $iconHoverColor;
  }

  &.isMinimized {
    transform: rotate(180deg);
  }

  &.disabled {
    color: $disabled;
    pointer-events: none;
  }
}

.faAngleLeft {
  height: 20px;
  min-width: 15px;
}

.faSignOutAlt {
  margin-right: 5px;
}

.signoutButton {
  color: $textColor;
  display: flex;
  align-items: center;
  height: 100%;
  cursor: pointer;
  justify-content: space-between;

  &:hover {
    color: $iconHoverColor;
  }
}

.footerSection {
  background: $grey01;
  display: flex;
  padding: 9px 20px 36px;
  justify-content: space-between;
}

.minimizedView {
  @include SmallScreens();
}
